export default {
	data() {
		return {
			orderList: [],
			type: 1 // 1 用户 2 司机
		}
	},
	/*
	 * orderType:1.同城货运，2同城快运，3快递
	 * @params type 1.同城货运，2同城快运，3快递 4司机订单
	 */
	onLoad(params) {
		let _this = this;
		let userInfo = uni.getStorageSync('userInfo');
		let data = {
			pageNum: "1",
			pageSize: '1000'
		}
		if(params.type.toString() === "4"){
			data.driverId = userInfo.driverId;
			_this.type = 2;
		}else{
			data.userId = userInfo.userId;
			data.orderType = params.type;
			_this.type = 1;
		}
		_this.request({
			url: _this.http.getOrderList,
			data: data,
			success: (res) => {
				if (res.code === '0') {
					_this.orderList = res.result;
				} else {
					uni.showToast({
						title: res.message,
						icon: 'none'
					});
				}
			}
		});
	},
	methods: {
		goOrder(id){
			if(this.type === 1){
				uni.navigateTo({
					url: '/pages/orderDetail/orderDetail?orderId=' + id
				})
			}else{
				uni.navigateTo({
					url: '/pages/robSuccess/robSuccess?orderId=' + id
				})
			}
		}
	}
}
